  components: {},
  computed: {},
  watch: {},
  data() {
    return {
      topic: "skill",
      bite: false,
      biteTimer: null,
    };
  },
  watch: {
    bite: {
      handler(bite) {
        if (bite) {
          if (this.biteTimer) {
            clearTimeout(this.biteTimer);
          }
          this.biteTimer = setTimeout(() => {
            this.bite = false;
          }, 500);
        }
      },
    },
  },
  mounted() {},
  beforeDestroy() {},
  methods: {
    open() {
      window.open("https://github.com/z7xisoo", "_blank");
    },
    handleTopic() {
      const topic = this.topic;
      switch (topic) {
        case "skill":
          this.topic = "works";
          break;
        case "works":
          this.topic = "information";
          break;
        case "information":
          this.topic = "skill";
          break;
      }
      this.bite = true;
    },
  },
};
</script>

